class IndexPage extends React.Component {
  constructor(props){
    super(props);

    this.state = {
      collections: [],
      aboutIsOpen: false,
      currentHover: 'index'
    }

    this.counter = 0;

    this.infiniteScroll = React.createRef();

    this.allCollections = [];

    this.handleScroll = this.handleScroll.bind(this);

  }

  componentWillMount(){

    let collectionFetchData = function(data) {
      this.projectName = data.title.text;
      this.projectYear = data.year.text;
      this.projectTechnique = data.technique.text;
      this.images = data.image_gallery;
    }

    this.allCollections = [];

    this.props.data.allPrismicImagegallery.edges.forEach((item) => {
      
      this.allCollections.push(new collectionFetchData(item.node.data));

    });

    this.setState({
      collections: [...this.allCollections, ...this.allCollections, ...this.allCollections]
    });


  }

  componentDidMount() {



    this.container = this.infiniteScroll;

    TweenLite.to(this.backgroundImageRef1,5,{x: '-100%',scale: 2, ease:Power3.easeInOut})

    setTimeout(() => {
      TweenLite.to(document.querySelector('.spacer-top'),1,{height: '0',ease:Power3.easeInOut, 
      onComplete: () => {
        document.querySelector('.footer').style.visibility = "visible";
        document.querySelector('body').style.position = "absolute";
        this.backgroundImageRef.style.opacity = 1;
        this.backgroundImageRef1.style.display = "none";
      }
    });

    },2500);



    document.querySelectorAll('.can-click').forEach((item)=>{
      item.addEventListener('mouseenter',this.mouseEnterLink);
      item.addEventListener('mouseleave',this.mouseLeaveLink);
    })

    
    

  //   setTimeout(() => {

  //     var text = new Blotter.Text("Hello", {
  //       family : "serif",
  //       size : 120,
  //       fill : "#171717"
  //     });
      
  //   var material = new Blotter.LiquidDistortMaterial();

  //   var blotter = new Blotter(material, { texts : text });

  //   var scope = blotter.forText(text);

  //   scope.appendTo(document.body);
  // },2500);


  }


  handleScroll = () => {

    this.handleThrottledScroll();

}

handleAboutPageClick = () => {

  this.setState(prevState => ({
    aboutIsOpen: !prevState.aboutIsOpen
  }))

}

mouseEnterInfinitySlider = (e) => {

  this.setState({
    currentHover: e
  })



}

mouseLeaveInfinitySlider = () => {

  this.setState({
    currentHover: 'index'
  })

}

mouseEnterLink = () => {
  this.setState({
    currentHover: 'link'
  })

}

mouseLeaveLink = () => {
  this.setState({
    currentHover: 'index'
  })

}


  render(){

    return(
      <Transition>
          <Mouse currentHover={this.state.currentHover}/>

        <About className="can-click" onClick={this.handleAboutPageClick}>
          <img src={PlusButton}/>
        </About>

        <StyledAboutMinus pose={this.state.aboutIsOpen ? 'openAbout' : 'closedAbout'} onClick={this.handleAboutPageClick}>
          <img src={MinusButton}/>
      </StyledAboutMinus>

      <SpacerTop className="spacer-top"/>
      <ContainerTop className='main-background'>   
      <BackgroundImage ref={div => this.backgroundImageRef1 = div} src={Logo}/>
      <BackgroundImage2 ref={div => this.backgroundImageRef = div} src={Logo}/> 
      <Footer className={'footer'}>
        <span className="can-click">©Anna Genger / website by Studio Karl Anders</span>
        <Socials>
        <span className="can-click">instagram</span>
        <span className="can-click">facebook</span>
        </Socials>
      </Footer>
      </ContainerTop>
      <Drawer style={{overflow: 'hidden'}} pose={this.state.aboutIsOpen ? 'openIndex' : 'closedIndex'}> 
      <SEO title="Anna Genger" keywords={[`Anna`, `Genger`, `Projects`]} />
      {/* <CornerSnipTop/> */}
      <Container>
      {/* <Controller>
      <Scene duration={'200%'} pin>     */}
      {/* <BackgroundImage2 ref={div => this.backgroundImageRef = div} src={Logo}/> */}
      {/* </Scene>
      </Controller> */}
      <ProjectDiv className={'infinite-scroll'} ref={this.infiniteScroll} onScroll={this.handleScroll}>
        {
          this.state.collections.map((item, index) => {
            return (
            <InfiniteSlider mouseLeave={this.mouseLeaveInfinitySlider} mouseEnter={this.mouseEnterInfinitySlider} key={index} backgroundColor='white' number={index} data={item}/>
              )
          })
        }
      </ProjectDiv>
      </Container>
      {/* <CornerSnipBottom/> */}
      </Drawer>
      {/* <Spacer/> */}
      <StyledOverlay onClick={this.handleAboutPageClick} pose={this.state.aboutIsOpen ? 'open' : 'closed'}/>
      <AboutPage open={this.state.aboutIsOpen}/>
      </Transition>
    )
  }
}

export default IndexPage

